import React, { useEffect, useMemo, useRef, useState } from "react";
import dotspattern from "../../assets/images/dots-pattern.png";

function Introduction({ setActiveLink, activeLink }) {
  const viewportRef = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);

  useEffect(() => {
    if (isInViewport) {
      setActiveLink({
        ...activeLink,
        banner: false,
        intro: true,
        lore: false,
        features: false,
        gravitars: false,
        roadmap: false,
        cta: false,
      });
    } else {
      // setActiveLink({
      //   ...activeLink,
      //   banner: false,
      //   intro: false,
      //   lore: false,
      //   features: false,
      //   gravitars: false,
      //   roadmap: false,
      //   cta: false,
      // });
    }
  }, [isInViewport]);

  function useIsInViewport(ref) {
    const [isIntersecting, setIsIntersecting] = useState(false);

    const observer = useMemo(
      () =>
        new IntersectionObserver(([entry]) =>
          setIsIntersecting(entry.isIntersecting)
        ),
      []
    );

    useEffect(() => {
      observer.observe(ref.current);

      return () => {
        observer.disconnect();
      };
    }, [ref, observer]);

    return isIntersecting;
  }

  return (
    <div id="intro" className="introduction-sec">
      <div className="container">
        <div className="introduction-desc">
          <h3 ref={viewportRef}>
            Gravitale is a virtual world with <span>stunning graphics</span>,
            <br />
            and <span>customization</span>.<br />
            Explore a vast world and compete in various
            <br />
            activities, <span>all in one platform</span>.
          </h3>
          <h1 className="placeholder-text">INTRO</h1>
        </div>
      </div>
      <img src={dotspattern} className="dots-pattern" />
    </div>
  );
}

export default Introduction;
