import * as THREE from "three";
import React, { useEffect, useRef, useState } from "react";
import { useGLTF, PerspectiveCamera, useAnimations } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";

export function GravitaleNew(scroll, props) {
  const extras = {
    receiveShadow: true,
    castShadow: true,
    "material-envMapIntensity": 0.2,
  };
  const group = useRef();
  const mesh = useRef();
  const { nodes, materials, animations } = useGLTF("./models/gravitalenew.glb");
  const { actions } = useAnimations(animations, group);
  const [hovered, set] = useState();
  const color = new THREE.Color();

  useEffect(() => void (actions["Animation"].play().paused = true), [actions]);

  useEffect(() => {
    // console.log('actions', actions)
    if (hovered)
      group.current.getObjectByName(hovered).material.color.set("white");
    document.body.style.cursor = hovered ? "pointer" : "auto";
  }, [hovered]);

  useFrame((state) => {
    actions["Animation"].time = THREE.MathUtils.lerp(
      actions["Animation"].time,
      actions["Animation"].getClip().duration * scroll.scroll.current * 3,
      0.05
    );
    // mesh.current.material.color.lerp(
    //   color.set(hovered === mesh.current.name ? "tomato" : "#ffffff"),
    //   hovered ? 0.1 : 0.05
    // );
    const et = state.clock.elapsedTime;
    // mesh.current.position.y = Math.sin((et + 0 * 2000) / 2) * 0.5;
    // mesh.current.rotation.x = Math.sin((et + 0 * 2000) / 3) / 10;
    mesh.current.rotation.y = Math.cos((et + 0 * 2000) / 2) / 10;
    mesh.current.rotation.z = Math.sin((et + 0 * 2000) / 3) / 10;
    //   group.current.children[0].children.forEach((child, index) => {});
  });

  return (
    <group ref={group} {...props} dispose={null}>
      <group name="Scene">
        <PerspectiveCamera
          name="Camera"
          makeDefault={true}
          far={100}
          near={0.1}
          fov={22.9}
          position={[-15.33, 3.29, 1.56]}
          rotation={[-2.98, -1.29, -2.99]}
        >
          <directionalLight
            castShadow
            position={[10, 20, 15]}
            shadow-camera-right={8}
            shadow-camera-top={8}
            shadow-camera-left={-8}
            shadow-camera-bottom={-8}
            shadow-mapSize-width={1024}
            shadow-mapSize-height={1024}
            intensity={1}
            shadow-bias={-0.0001}
          />
        </PerspectiveCamera>
        <mesh
          onPointerOver={(e) => (e.stopPropagation(), set(e.object.name))}
          onPointerOut={(e) => (e.stopPropagation(), set(null))}
          ref={mesh}
          name="1"
          geometry={nodes["1"].geometry}
          material={materials.lambert1}
          rotation={[Math.PI / 2, 0, 0]}
          scale={0.01}
          {...extras}
        />
      </group>
    </group>
  );
}

useGLTF.preload("./models/gravitalenew.glb");
